<template>
  <v-card>
    <v-container fluid>
      <v-row align="center">
        <v-col
          cols="12"
          class="pb-0 mb-0"
        >
          <v-select
            :items="essayTypes"
            label="Essay (any type)"
            solo
            class="py-0 my-0"
          />
        </v-col>
        <v-col
          cols="12"
          class="pb-0 mb-0"
        >
          <v-row align="center">
            <v-col cols="6">
              <v-select
                :items="due"
                label="hours"
                class="py-0 my-0"
                solo
              />
            </v-col>
            <v-col cols="6">
              <v-text-field
                outlined
                dense
                single-line
                v-model="time"
              >
              </v-text-field>
            </v-col>
          </v-row>
        </v-col>
        <v-col>
          <v-row>
            <v-col cols="2">
              <v-btn
                :class="{'disable-events': inactive}"
                @click="decrementPages"
              >
                <v-icon>mdi-minus</v-icon>
              </v-btn>
            </v-col>
            <v-col cols="1" />
            <v-col cols="6">
              <v-input
                v-model="pages"
                :hide-details="False"
              >
                {{ pages }} Pages / {{ pages * 275 }} Words
              </v-input>
            </v-col>
            <v-col cols="2">
              <v-btn @click="incrementPages">
                <v-icon>mdi-plus</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-col>
        <v-col
          cols="12"
        >
          <v-row>
            <v-col
              cols="4"
              class="price"
            >
              $ {{ price }}
            </v-col>
            <!-- <v-spacer /> -->
            <v-col cols="6">
              Your minimum Price
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </v-container>
  </v-card>
</template>

<script>
  export default {
    name: 'Calculator',
    data: () => ({
      due: ['Hours', 'Days', 'Weeks'],
      time: 1,
      essayTypes: [
        'Essay (any type)',
        'Admission Essay',
        'Annotated Bibliography',
        'Article Review',
        'Book / Movie Review',
        'Business Plan',
        'Case Study',
        'Creative Writing',
        'Critical Thinking / Review',
        'Literature Review',
        'Presentation or Speech',
        'Reflective Writing',
        'Report',
        'Research Paper',
        'Research Proposal',
        'Term Paper',
        'Thesis / Dissertation',
        'Other',
      ],
      pages: 1,
      cpp: 7.5,
    }),
    computed: {
      price: function () {
        return this.cpp * this.pages
      },
      inactive: function () {
        return this.pages <= 1
      },
    },
    methods: {
      incrementPages: function () {
        this.pages += 1
      },
      decrementPages: function () {
        this.pages -= 1
      },
    },
  }
</script>

<style scoped>
.price {
    font-size: 24px;
    font-weight: 700;
    line-height: 29px;
    color: #212121;
}

.disable-events {
  pointer-events: none;
  background-color: rgba(0,0,0,.12) !important;
  color: gray;
}

</style>
